.header {
  position: fixed;
  display: flex;
  align-items: center;
  left: 50%;
  transform: translateX(-50%);
  width: 75%;
  margin: 0 auto;
  background: white;
  height: calc(110px - 24px);
  box-shadow: 0px 4px 4px 0px #00000040;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  padding: 12px;
}